<template>
  <div class="cons">
    <div class="searchCon">
      <div class="searchItem">
        <div class="searchLabel">level</div>
        <div class="searchInput searchSelect">
          <el-select v-model="search.level" :popper-append-to-body = false placeholder="">
            <el-option
              v-for="item in selectOptions"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
        </div>
      </div>
      <div class="searchItem">
        <div class="searchInput dateInput">
          <el-date-picker
            v-model="daterangeVal"
            type="daterange"
            @change="onPick"
            value-format="yyyy-MM-dd"
            range-separator=""
            start-placeholder=""
            end-placeholder="">
          </el-date-picker>
        </div>
      </div>
      <div class="searchBtnCon">
        <el-button type="primary" class="CoBtn searchCoBtn" @click="searchList()"></el-button>
        <el-button class="CoResetBtn" @click="reset()"></el-button>
        <el-button class="CoResetBtn" @click="share()"></el-button>
      </div>
    </div>
    <div class="totalTableSize">
      <div class="tipIcon"></div>
      <div class="totalCon">
        {{page.total}}
      </div>
    </div>
    <div class="tableCon">
      <commonTable :columns="columns" :data="tableData" :pager="page"
                   @handleCurrentChange="handleCurrentChange">

        <el-table-column slot="table_oper" align="center" label="" width="200" :resizable="false" fixed="right">
          <template slot-scope="scope">
            <div class="tableBtn" @click="detail('request',scope.row.request)">
              <el-button type="text">request</el-button>
            </div>
            <el-divider direction="vertical"></el-divider>

            <div class="tableBtn" @click="detail('stack',scope.row.stack)">
              <el-button type="text">stack</el-button>
            </div>
            <el-divider direction="vertical"></el-divider>

            <div class="tableBtn" @click="detail('',scope.row)">
              <el-button type="text"></el-button>
            </div>
          </template>
        </el-table-column>
      </commonTable>
    </div>
    <div class="mask" v-show="showPopBox"></div>
    <div class="popBox"  v-show="showPopBox">
      <div class="popBoxTitle">
        <span>{{popTitle}}</span>
        <div class="closeBtn" @click="showPopBox = false">X</div>
      </div>
      <div class="popCon" v-if="popTitle === 'request'">
        <div v-for="item in popArrayTwo">
          <div class="popLabel">{{item.name}}</div>
          <div class="popData">{{item.val}}</div>
        </div>

<!--        <div class="popLabel">body</div>-->
<!--        <div class="popData">{{popData.body}}</div>-->
<!--        <div class="popLabel">header</div>-->
<!--        <div class="popData">{{popData.header}}</div>-->
      </div>
      <div class="popCon" v-else-if="popTitle === 'stack'">
        <div class="popLabel">stack</div>
        <div class="popData">{{popData}}</div>
      </div>
      <div class="popCon" v-else-if="popTitle === ''">
        <div class="popItem" v-for="item in popArray" >
          <div class="popLabel">{{item.name}}</div>
          <div class="popData">{{item.val}}</div>
        </div>

      </div>
    </div>
  </div>
</template>

<script>
  import SYSTEM from 'admin/service/system-service.js'
  import wx from 'weixin-js-sdk';
  export default {
    name: "SystemLog",
    data() {
      return {
        search: {
          level: '',
          beginTime:'',
          endTime:''
        },
        daterangeVal:'',
        selectOptions : [
          {
            value: 'WARNING',
            label: 'WARNING'
          }, {
            value: 'ERROR',
            label: 'ERROR'
          }, {
            value: 'INFO',
            label: 'INFO'
          }
        ],
        columns: [
          {prop: 'originalUrl', label: '', width: '200', align: 'left'},
          {prop: 'message', label: 'message', align: 'left',width: '400'},
          {prop: 'date', label: '', align: 'left',width: '180'},
        ],
        tableData: [],
        page: {
          page: 1,
          unit: 10,
          total: 0
        },
        showPopBox:false,
        popData:{},
        popTitle:'',
        popArray:[],
        popArrayTwo:[]
      }
    },
    mounted() {

    },
    created(){
      this.getList();
      SYSTEM.checkWechat()
        .then(res => {
          console.log('res',res);
          if(res.code === "0"){
            wx.config({
              debug : true, //,apialertpclogpc
              appId : res.data.appID, //
              timestamp : res.data.timestamp, //
              nonceStr : res.data.noncestr, //
              signature : res.data.signature,//1
              jsApiList : [ 'onMenuShareAppMessage','onMenuShareTimeline' ]//JSJS2
            });
          }
        })
      wx.ready(function () {
        //
        wx.onMenuShareAppMessage({
          title: 'xxx', // 
          desc: '', // 
          link: 'https://www.beihaozhuan.com', // JS
          imgUrl: 'https://beihaozhuan.oss-cn-zhangjiakou.aliyuncs.com/images/13198600801258.jpg', // 
          type: 'link', // ,musicvideolinklink
          dataUrl: '', // typemusicvideo
          success: function () {
            // 
            alert('');
          },
          cancel: function () {
            // 
            alert('');
          }
        });
      });
    },
    methods: {
      //
      getList(){
        let data ={
          'page' : this.page.page,
          'unit': 10,
          'level':this.search.level,
          'beginTime' : this.search.beginTime,
          'endTime':this.search.endTime
        };
        SYSTEM.getLogger(data)
          .then(res => {
            console.log('res',res);
            if(res.code === "0"){
              this.tableData = res.data;
              this.page.total = res.count;
            }
          })
      },
      //
      searchList(){
        this.page.page = 1;
        this.getList();
      },
      //
      reset(){
        this.search.level = '';
        this.search.beginTime = '';
        this.search.endTime = '';
      },
      onPick(date){
        this.search.beginTime = date[0];
        this.search.endTime = date[1];
      },
      // 
      handleCurrentChange(val) {
        this.page.page = val;
        this.getList();
      },
      //
      goAddAds(){
        this.$router.push({
          name:'AddAdministrators',
        })
      },
      //
      pSubmit(uuid) {
        let data = {
          'uuid' : uuid,
          'activity': 'disable'
        };
        SYSTEM.setUserStatus(data)
          .then(res => {
            console.log('res',res);
            if(res.code === "0"){
              this.$message({
                type: 'success',
                message: ''
              });
              this.getList();
            }
          })
      },
      //
      detail(title,data){
        this.showPopBox = true;
        this.popTitle = title;
        this.popData = data;
        if(title === ''){
          let array = [];
          for(let item in data){
            if(item != 'request' && item != 'stack'){
              let a = {
                'name' : item,
                'val':data[item]
              };
              array.push(a);
            }
          }
          this.popArray = array;
        }else if(title === 'request'){
          let arrayTwo = [];
          for(let item in data){
            let a = {
              'name' : item,
              'val':data[item]
            };
            arrayTwo.push(a);
          }
          this.popArrayTwo = arrayTwo;
        }

      },
      share(){
        wx.ready(function () {
          //
          wx.onMenuShareAppMessage({
            title: 'xxx', // 
            desc: '', // 
            link: 'https://www.beihaozhuan.com', // JS
            imgUrl: 'https://beihaozhuan.oss-cn-zhangjiakou.aliyuncs.com/images/13198600801258.jpg', // 
            type: 'link', // ,musicvideolinklink
            dataUrl: '', // typemusicvideo
            success: function () {
              // 
              alert('');
            },
            cancel: function () {
              // 
              alert('');
            }
          });
        });
      }
    }

  }
</script>

<style lang="css" scoped>
  .cons{
    overflow: hidden;
  }
  .searchItem{
    margin-right: 32px;
  }
  .searchInput{
    width: 168px;
  }
  .dateInput{
    width: 100%;
  }
  .searchSelect{
    width: 136px;
  }
  .searchCoBtn{
    margin-left: -20px;
  }
  .mask{
    width: 100%;
    height: 100%;
    background: #000;
    opacity: .4;
    z-index: 9;
    position: fixed;
    top: 0;
    left: 0;
    bottom: 0;
  }
  .popBox{
    width: 1000px;
    height: 600px;
    background: #ffffff;
    position: fixed;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    margin: auto;
    z-index: 99;
    overflow-x: hidden;
    overflow-y: auto;
  }
  .popBoxTitle{
    width: 1000px;
    height: 50px;
    line-height: 50px;
    background: #F0F2F5;
    /*padding: 0 150px 0 20px;*/
    position: fixed;
    overflow: hidden;
  }
  .popBoxTitle span{
    margin-left: 20px;
  }
  .closeBtn{
    float: right;
    cursor: pointer;
    margin-right: 20px;
  }
  .popCon{
    margin-top: 50px;
    padding: 10px 20px;
    overflow: auto;
  }
  .popLabel{
    height: 40px;
    line-height: 40px;
  }
  .popData{
    border: 1px solid #F0F2F5;
    line-height: 30px;
    padding: 10px;
    word-break: break-all;
  }
  .popItem{
    width: 49%;
    float: left;
    margin-right: 5px;
  }
</style>



